<template>
	<div id="publish">
		<common-header :title='title'></common-header>
		
		<div class="publish-content">
			<div class="publish-text">
				<img class="goods-img" src="../../assets/pic.png"/>
				<textarea placeholder="礼物怎么样？说说您的看法吧~" v-model="textValue"></textarea>
			</div>
			<div class="publish-imgs clearfix">
				<ul class="clearfix">
					<!--<li>
						<img class="publish-icon" src="../../assets/pic.png"/>
						<div class="del-btn">
							<img src="../../assets/del-icon.png"/>
						</div>
					</li>
					<li>
						<img class="publish-icon" src="../../assets/common_btn_add_pic@2x.png"/>
						<input 
							type="file"
							accept="image/*" 
							name="file[]" 
							id="publish-img" 
							@change="uploadImg($event,index)"/>
					</li>-->
					<li v-for="(item,index) in imgList">
						<img class="publish-icon" :src="item"/>
						<div class="del-btn" @click="delImg(item,index)">
							<img src="../../assets/del-icon.png"/>
						</div>
					</li>
					<li v-show="imgList.length<6">
						<img class="publish-icon" src="../../assets/common_btn_add_pic@2x.png"/>
						<input 
							type="file"
							accept="image/*" 
							name="file[]" 
							id="publish-img" 
							@change="uploadImg($event)"
							ref="imgInput"/>
					</li>
				</ul>
			</div>
		</div>
		<div class="publish-score">
			<span>评分</span>
			<span>
				<i class="star" :class="{active:starIndex >= 1}" @click="selectStar(1)"></i>
				<i class="star" :class="{active:starIndex >= 2}" @click="selectStar(2)"></i>
				<i class="star" :class="{active:starIndex >= 3}" @click="selectStar(3)"></i>
				<i class="star" :class="{active:starIndex >= 4}" @click="selectStar(4)"></i>
				<i class="star" :class="{active:starIndex >= 5}" @click="selectStar(5)"></i>
			</span>
		</div>
		<div class="publish-btn" :class="{active : isComplete}" @click="commentSubmit">发表评价</div>
	</div>
</template>

<script>
import CommonHeader from '@/components/Header/CommonHeader'
	
export default {
	data(){
		return {
			starIndex:-1,	
			title:"发表评价",
			textValue:'',
			imgList:[],
			fileList:[],
			urlList:[],
			upLoadUrlList:[],
			imgIdList:[],
		}
	},
  components: {
  	CommonHeader
  },
  computed:{
  	isComplete() {
			if(this.textValue.length > 0 && this.starIndex != -1) {
				return true;
			}else{
				return false;
			}
		}
  },
  methods:{
  	selectStar(index){
  		this.starIndex = index;
  	},
  	//上传图片
			uploadImg() {
				var oFile = document.getElementById("publish-img").files[0];
				var filename = oFile.name
				
				this.fileList.push(oFile)
				
				this.oFile = oFile
				this.filename = filename

				let params = {
					a: 1,
					type: 1,
					filename: filename,
					oFile: oFile,
				}
				this.getExternalUploadObject(params)

				let oFReader = new FileReader();
				oFReader.readAsDataURL(oFile);
				oFReader.onload = (oFREvent) => {
					let imgData = oFREvent.currentTarget.result
					this.imgList.push(imgData)
					oFile.value = '';
					document.getElementById("publish-img").value = ''
				};
			},
			delImg(item,index){
				this.imgList.splice(index,1)
				this.urlList.splice(index,1)
				this.fileList.splice(index,1)
			},
			getExternalUploadObject(params){
				this.$Api.getExternalUploadObject(params).then((res) => {
					console.log(res)
					if(res.q.s == 0) {
						this.cos = res.q.cos
						let urlObj = {
							accessUrl : res.q.cos.accessUrl,
							sourceUrl : res.q.cos.sourceUrl
						}
						console.log(urlObj)
						this.urlList.push(urlObj)
						this.upLoadUrlList.push(res.q.cos.url)
					}
				})
			},
			getExternalFilesSubmit(){
				let $this = this
				let fileInfos = []
				this.urlList.forEach((item)=>{
					let fileInfo ={
						accessUrl : item.accessUrl,
						sourceUrl : item.sourceUrl
					}
					fileInfos.push(fileInfo)
				})
				console.log(fileInfos)
				this.$Api.getExternalFilesSubmit(1,1,fileInfos).then((res)=>{
					console.log(res)
					if(res.q.s == 0){
						let files = res.q.files
						files.forEach((item)=>{
							console.log(item.id)
							this.imgIdList.push(item.id)
						})
						
						for (let i = 0; i < this.fileList.length; i++) {
								$this.$upLoadToCos(this.upLoadUrlList[i],this.fileList[i])
						}
						
						this.getCommentSubmit()
//						let id = res.q.files[0].id
						//上传图片
//						this.upLoadToCos()
						//更新头像
//						this.SetImage(id)
					}
				})
			},
			commentSubmit(){
				if(this.isComplete){
					this.getExternalFilesSubmit()
				}
			},
			getCommentSubmit(){
				let a = 11
				let id = this.$route.params.id
				let comments = []
				
				let comment = {
					userId : 0,
					starNum : this.starIndex,
					content : this.textValue,
					imageIds : this.imgIdList
				}
				comments.push(comment)
				this.$Api.CommentSubmit(a,id,comments).then((res)=>{
					console.log(res)
				})
			},
  }
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#publish{
		height: 100%;
		background: #f0f0f0;
		.publish-content{
			width: 100%;
			/*min-height: 5.87rem;*/
			padding: 0.29rem 0 0 0.4rem;
			background: #fff;
			.publish-text{
				display: flex;
				height: 2.67rem;
				.goods-img{
					width: 1.07rem;
					height: 1.07rem;
					border-radius: 0.11rem;
					/*border: 1px solid $border-color;*/
					margin-right: 0.4rem;
				}
				>textarea{
			    background: #fff;
			    border: 0;
			    width: 100%;
			    height: 100%;
			    box-sizing: border-box;
			    resize: none;
			    font-size: 0.37rem;
			    color: #333;
			    line-height: 0.53rem
				}
			  >textarea::-webkit-input-placeholder {
			    color:#999;
			  }
			}
			.publish-imgs{
				margin-top: 0.4rem;
				ul{
					>li{
						position: relative;
						float: left;	
						margin-right: 0.4rem;
						margin-bottom: 0.4rem;
					}
					.del-btn{
						position: absolute;
						top: 0;
						right: 0;
						width: 0.43rem;
						height: 0.43rem;
						background: rgba(0, 0, 0, 0.3);
					}
					input{
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0;
						width: 2.0rem;
						height: 2.0rem;
					}
				}
				.publish-icon{
					width: 2.0rem;
					height: 2.0rem;
				}
			}
		}
		.publish-score{
			position: relative;
			margin-top: 0.27rem;
			width: 100%;
			height: 1.23rem;
			line-height: 1.23rem;
			background: #fff;
			padding: 0 0.4rem;
			font-size: 0.4rem;
			color: #333;
			>span{
				&:nth-of-type(2){
					position: absolute;
					top: 50%;
					right: 0.4rem;
					transform: translateY(-50%);
				};
			}
			.star{
				display: inline-block;
				background-image: url(../../assets/my_btn_star_gray@2x.png);
				background-size: 0.59rem 0.53rem;
				background-repeat: no-repeat;
				width: 0.59rem;
				height: 0.53rem;
				margin-left: 0.24rem;
			}
			.active{
				background-image: url(../../assets/my_btn_star_yellow@2x.png);
			}
		}
		.publish-btn{
			position: fixed;
			bottom: 0;
			width: 100%;
			font-size: 0.45rem;
			height: 1.31rem;
			line-height: 1.31rem;
			text-align: center;
			color: #fff;
			background: #ccc;

		}
		.active{
			@include bg-color;
		}
	}
</style>


